import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class DeviceDetailsService {
  tokenVal = sessionStorage.getItem('adal.idtoken');

  header = new HttpHeaders({
    'Content-Type': 'application/json',
    Authorization: 'Bearer ' + this.tokenVal,
    'Access-Control-Allow-Origin': '*',
  });

  constructor(public httpClient: HttpClient) {}
  getDevicesData(): Observable<any> {
    return this.httpClient.get<any>('https://webapp-20017238.azurewebsites.net/api/v1/getdata', { headers: this.header });
  }
}
